import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
export default {
	components: {
		uniNavBar
	},
	data() {
		return {
			phone: '',
			detailData: {}
		};
	},
	onLoad(options) {
		this.detailData = JSON.parse(options.data);
		console.log(this.detailData);
		this.phone = this.detailData.phone;
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		//保存
		submitJunmp() {
			console.log('保存成功');
			uni.showLoading({
				title: 'loading'
			});
			var obj = { phone: this.phone };
			//发起请求
			this.$http
				.post('/interface/rest/http/xlwb/xlgc-wb-xcx-grzx-xgsjh.htm', obj)
				.then(res => {
					uni.hideLoading();
					console.log(res.data);
					if (res.data.msgState == 1) {
						uni.showToast({
							title: res.data.msg,
							duration: 2000
						});
						uni.navigateBack({
							delta: 1
						});
					}else{
						uni.showToast({
							 title: res.data.msg,
							 duration: 2000,
							 icon:'none'
						})
					}
					
				})
				.catch(err => {
					console.log(err);
					uni.hideLoading();
					uni.showToast({
						 title: "请求失败",
						 duration: 2000,
						 icon:'none'
					})
				});
		}
	}
};
